.title {
    display: flex;
    align-items: center;
    color: #ffffff;
    flex-grow: 1;
    font-size: 24px;
    font-style: normal;
    letter-spacing: 0.3vw;
    text-decoration: none;
}

.header {
    width: 90vw;
    color: #eee;
    display: flex;
    align-items: center;
    padding: 40px 5%;
    position: sticky;
    top: 0;
    background-color: #222;
}

.icon {
    margin-right: 8px;
}
